<template>
	<transition name="fade">
		<div v-if="display" class="flux-controls">
			<div class="previous" @click="vf.showImage('previous')"></div>
			<div :class="autoplayClass" @click="vf.toggleAutoplay()"></div>
			<div class="next" @click="vf.showImage('next')"></div>
		</div>
	</transition>
</template>

<script>
	export default {
		props: {
			slider: { type: Object }
		},

		computed: {
			vf: function() {
				if (this.slider)
					return this.slider;

				if (this.$parent.$options.name === 'VueFlux')
					return this.$parent.loaded? this.$parent : undefined;

				console.warn('slider not referenced, check https://github.com/deulos/vue-flux/wiki/FluxControls for help');

				return undefined;
			},

			display: function() {
				if (!this.vf)
					return false;

				if (this.vf.mouseOver !== true)
					return false;

				if (this.vf.transition.current !== undefined)
					return false;

				if (this.vf.index !== undefined && this.vf.index.visible)
					return false;

				return true;
			},

			autoplayClass: function() {
				return this.vf.config.autoplay? 'pause' : 'play';
			}
		}
	};
</script>

<style lang="scss">
	.vue-flux .flux-controls {
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		height: 50px;
		margin-top: -25px;
		text-align: center;
		z-index: 100;

		&.fade-enter, &.fade-leave-to {
			opacity: 0;
		}

		&.fade-enter-active, &.fade-leave-active {
			transition: opacity 0.3s ease-in;
		}

		.previous,
		.play,
		.pause,
		.next {
			position: absolute;
			top: 0;
			width: 50px;
			height: 50px;
			cursor: pointer;
			border-radius: 50%;
			background-color: rgba(0, 0, 0, 0.6);
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 40%;
			transition: background-color 0.2s ease-in;
		}

		.previous {
			left: 25px;
			background-image: url(data:image/png;base64,);
		}

		.play {
			position: relative;
			display: inline-block;
			background-image: url(data:image/png;base64,);
		}

		.pause {
			position: relative;
			display: inline-block;
			background-image: url(data:image/png;base64,);
		}

		.next {
			right: 25px;
			background-image: url(data:image/png;base64,);
		}

		.previous:hover,
		.play:hover,
		.pause:hover,
		.next:hover {
			background-color: rgba(0, 0, 0, 0.9);
		}
	}
</style>
